import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { Router, NavigationEnd, ActivatedRoute } from '@angular/router';
import { locales } from './locales.values';
import { filter } from 'rxjs/operators';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  locales = [];
  currentUrl = "";
  title = 'routing';
  location: Location;
  navEnd: Observable<NavigationEnd>;

  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute,
    location: Location
  ) {
    this.location = location;
    // Create a new Observable the publishes only the NavigationStart event
    this.navEnd = router.events.pipe(
      filter(evt => evt instanceof NavigationEnd)
    ) as Observable<NavigationEnd>;

  }

  ngOnInit() {
    this.locales = locales;
    this.navEnd.subscribe(evt => {
      // TODO: find better way to access route data
      if (this.activatedRoute.firstChild.firstChild && this.activatedRoute.firstChild.firstChild.snapshot.data.postMessage) {
         window.parent.postMessage(location.href, '*');
      }
      this.currentUrl = this.router.url;
      /*if(window.location.pathname) {
        var pathArray = window.location.pathname.split('/');
        if(pathArray) {
          this.localeCode = pathArray[1];
        } else{
          this.localeCode = 'en';
        }
      }*/
    });
  }
}
